.overlay-nav {
  background-color: #ffffff;
  transition: transform 225ms cubic-bezier(0, 0, 0.2, 1); }
  .overlay-nav__toggle {
    display: block;
    position: fixed;
    right: 25px;
    bottom: 25px;
    width: 60px;
    height: 60px;
    color: black;
    background-color: #fff;
    text-indent: -9999px;
    border-radius: 50%; }
    .overlay-nav__toggle--active {
      z-index: 1000;
      background-color: #fff;
      transition: background-color 225ms cubic-bezier(0.25, 0.1, 0.25, 1); }
      .overlay-nav__toggle--active::after {
        transform: rotate(-45deg); }
      .overlay-nav__toggle--active::before {
        transform: rotate(45deg); }
    .overlay-nav__toggle::before {
      content: '';
      position: absolute;
      top: 50%;
      right: 0;
      left: 0;
      width: 20px;
      height: 14px;
      margin: -10px auto 0;
      border-top: 2px solid;
      margin-top: 0;
      height: 0;
      border-bottom: 0;
      border-bottom-color: currentcolor;
      border-color: #000;
      transition: border-color 225ms cubic-bezier(0.25, 0.1, 0.25, 1); }
    .overlay-nav__toggle::after {
      content: '';
      position: absolute;
      top: 50%;
      right: 0;
      left: 0;
      width: 20px;
      height: 14px;
      margin: -7px auto 0;
      border-top: 2px solid;
      margin-top: 0;
      height: 0;
      border-bottom: 0;
      border-color: #000;
      transition: border-color 225ms cubic-bezier(0.25, 0.1, 0.25, 1); }
  .overlay-nav__list {
    list-style: none;
    margin: 0;
    padding: 0;
    padding-top: 100px; }
  .overlay-nav__item {
    margin: 0;
    padding: 0;
    display: block;
    width: 100%; }
  .overlay-nav__link {
    text-decoration: none;
    color: inherit;
    display: block;
    padding: 10px 0;
    text-align: left;
    text-decoration: none;
    font-size: 36px; }

